.container {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
}

.backgroundimage {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(../../img/about.jpg);
    background-size: cover;
    filter: blur(4px);

}

.content {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    height: 100%;
}

.content h1 {
    font-size: 4.5vw;
    color: #f0e7e7;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
    margin-top: 15vw;
    margin-left: 7vw;
    font-weight: bold;
    font-style: italic;
    font-family: Georgia, 'Times New Roman', Times, serif;
}

.content hr {
    color: white;
    margin-left: 10vw;
    width: 17vw;
}

.galleryContainer {
    margin: 3rem 0;
}

.galleryContainer h1 {
    font-weight: bold;
    font-style: italic;
    text-align: center;
    font-size: 3vw;
    font-family: Georgia, 'Times New Roman', Times, serif;
}

.lastContainer h1 {
    font-weight: bold;
    font-style: italic;
    text-align: center;
    font-size: 3vw;
    font-family: Georgia, 'Times New Roman', Times, serif;
}

.icon {
    display: inline;
    color: #FF770F;
    margin: 10px;
}

.h1 {
    font-weight: bold;
    font-style: italic;
    font-size: 2rem;
    font-family: Georgia, 'Times New Roman', Times, serif;
    margin: 1.6rem 5rem;
}



@media screen and (max-width: 550px) {

    .content h1 {
        font-size: 35px;
        margin-top: 115px;
        margin-left: 60px;
    }

    .content hr {
        color: white;
        margin-left: 72px;
        width: 140px;
    }

    .galleryContainer h1 {
        font-size: 25px;
    }

    .h1 {
        margin: 1.6rem 0rem;
        text-align: center;
        font-size: 25px;

    }

    .icon {
        display: inline;
        color: #FF770F;
        margin: 10px 5px;
    }

    


}