  .container {
      position: relative;
      width: 100%;
      height: 100vh;
      overflow: hidden;
  }

  .backgroundimage {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQNGDf1Fl0GPzwBqes2FVuv1E2BokBrVL_oPg&usqp=CAU");
      background-size: cover;
      background-repeat: no-repeat;
      filter: blur(6px);
  }

  .content {
      position: relative;
      z-index: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      height: 100%;
  }

  .content h1 {
      font-size: 60px;
      color: #fff;
      text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
      text-align: center;
      font-weight: bold;
      font-style: italic;
  }

  .content h2 {
      font-size: 36px;
      color: #dbb33c;
      text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
      text-align: center;
      font-weight: bold;
  }


  .content span {
      margin: .7rem;
      color: white;
      font-size: 2vw;
      text-align: center;
    font-weight: bold;
  }

  .hr1,
  .hr2 {
      color: white;

  }

  hr {
      width: 14vw;
  }

  .fixedImg {
      width: 100%;
      height: 450px;
  }

  @media screen and (max-width: 550px) {


      .content h1 {
          font-size: 40px;
      }

      .content h2 {
          font-size: 25px;

      }

      .content span {
          margin: .7rem;
          font-size: 15px;

      }

      hr {
          width: 5rem;

      }
  }